
.progress[data-v-085203de] {
  height: 20px;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 4px;
  box-shadow: inset 0 1px 2px rgba(0,0,0,0.1);
}
.progress .progress-bar[data-v-085203de] {
  float: left;
  height: 100%;
  font-size: 12px;
  line-height: 20px;
  color: #fff;
  text-align: center;
  box-shadow: inset 0 -1px 0 rgba(0,0,0,0.15);
  -webkit-transiton: width 0.6s ease-in-out;
  -o-transiton: width 0.6s ease-in-out;
  transiton: width 0.6s ease-in-out;
}
.progress.progress-striped .progress-bar[data-v-085203de] {
  background-image: linear-gradient(45deg, rgba(255,255,255,0.15) 25%, transparent 25%, transparent 50%, rgba(255,255,255,0.15) 50%, rgba(255,255,255,0.15) 75%, transparent 75%, transparent);
  background-size: 40px 40px;
  animation: progress-bar-stripes 2s linear infinite;
}


