output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  box-sizing: border-box;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}
html {
  height: 100%;
}
body {
  line-height: 1;
  background: #d9f0ff;
  height: auto;
  min-height: 100%;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: #7e8c8d;
  -webkit-backface-visibility: hidden;
  text-decoration: none;
}
li {
  list-style: none;
}
body {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
button {
  height: 34px;
  line-height: 34px;
  padding: 0 20px;
  background: #fff;
  border-width: 1px;
  border-style: solid;
  border-color: #d9d9d9;
  margin-right: 10px;
}
button .btn-orange {
  color: #fff;
  background: #fa7d3c;
}
button .btn-orange:hover {
  background: #f4722e;
}
button .btn-default {
  box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
}
button .btn-default:hover {
  box-shadow: 0px 2px 2px rgba(0,0,0,0.3);
}
@keyframes jump {
0% {
    transform: translateY(0);
}
75% {
    transform: translateY(-50%);
}
100% {
    transform: translateY(0);
}
}
@keyframes diving {
0% {
    transform: translateY(0);
}
75% {
    transform: translateY(50%);
}
100% {
    transform: translateY(0);
}
}
a {
  color: #2c3e50;
  text-decoration: none;
}
input,
button[disabled] {
  cursor: not-allowed;
}
.flex-grow {
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.text-ellipsis-mul {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: break;
  display: -webkit-box;
  -webkit-line-clamp: 2;
}
.btn-radius-t-l {
  border-top-left-radius: 50% 100%;
}
.btn-radius-b-r {
  border-bottom-right-radius: 50% 100%;
}
.btn-radius-brackets {
  border-radius: 0.5em/1em;
}
.btn-radius-skew {
  border-radius: 5em 1em/5em 1em;
}
.btn-radius-trapezoid {
  border-top-left-radius: 25% 100%;
  border-top-right-radius: 25% 100%;
}
.btn-gradient.glassy {
  text-shadow: rgba(255,255,255,0.5) 0 -1px 0, rgba(0,0,0,0.18) 0 0.18em 0.15em;
  position: relative;
}
.btn-gradient:after {
  content: "";
  position: absolute;
  width: 90%;
  height: 60%;
  top: 0;
  left: 5%;
  background-image: -moz-linear-gradient(left, rgba(255,255,255,0.55), rgba(255,255,255,0) 50%, rgba(255,255,255,0) 80%, rgba(255,255,255,0.5));
  background-image: gradient(linear, 0% 0, 100% 0, from(rgba(255,255,255,0.55)), to(rgba(255,255,255,0.5)), color-stop(0.5, rgba(255,255,255,0)), color-stop(0.8, rgba(255,255,255,0)));
}
.icon-toggle-animation-cross {
  filter: drop-shadow(0 0 10px #f00);
}
.icon-toggle-animation-cross .toggle-content {
  width: 40px !important;
  height: 20px !important;
  background: #87cefa;
}
.icon-toggle-animation-cross .toggle-content:after {
  width: 40px !important;
  height: 20px !important;
  transform: rotate(90deg);
  background: #87cefa;
}
.icon-toggle-animation-jump {
  animation-name: jump;
}
.icon-toggle-animation-jump .toggle-content {
  transform: rotate(45deg);
  border-left: 2px solid #000;
  border-top: 2px solid #000;
}
.icon-toggle-animation-jump .toggle-content:after {
  border-left: 2px solid #000;
  border-top: 2px solid #000;
}
.icon-toggle-animation-diving {
  animation-name: diving;
}
.icon-toggle-animation-diving .toggle-content {
  transform: rotate(45deg);
  border-right: 2px solid #000;
  border-bottom: 2px solid #000;
}
.icon-toggle-animation-diving .toggle-content:after {
  border-right: 2px solid #000;
  border-bottom: 2px solid #000;
}
.icon-toggle {
  border: none;
  background: transparent;
  padding: 0;
  margin: 0 auto;
  width: 2em;
  text-align: center;
  vertical-align: middle;
  font-size: 12px;
}
.icon-toggle:hover {
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}
.icon-toggle .toggle-content {
  width: 1em;
  height: 1em;
  position: relative;
  display: inline-block;
}
.icon-toggle .toggle-content:after {
  position: absolute;
  left: 4px;
  top: 4px;
  content: " ";
  width: 1em;
  height: 1em;
}
@media screen and (min-width: 1366px) {
.block {
    background-color: #fff;
    margin: 15px 0;
    padding: 20px;
    width: 100%;
    height: auto;
}
.block.nobg {
    background: transparent;
}
button {
    width: 82px;
}
.btn-default {
    box-shadow: 0px 1px 2px rgba(0,0,0,0.1);
}
.btn-default:hover {
    box-shadow: 0 1px 2px rgba(0,0,0,0.15);
}
.btn-orange {
    color: #fff;
    background: #fa7d3c;
    background: linear-gradient(top bottom, #fa7d3c 0%, #f55f10 100%);
}
.btn-orange:hover {
    background: #f4722e;
    background: linear-gradient(top bottom, #f4722e 0%, #f05807 100%);
}
.link {
    color: #87cefa;
}
.img-content {
    width: 100%;
    display: inline-block;
    border: 1px solid #e5e9ef;
    border-radius: 4px;
    padding: 2px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}
.img-content-sm {
    display: inline-block;
    width: 104px;
    height: 104px;
    border: 1px solid #e5e9ef;
    border-radius: 4px;
    padding: 2px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-right: 16px;
}
}
