@media screen and (min-width: 1366px) {
#app {
    height: 100%;
    background-image: url("/TeamSII/dist/static/images/bg.jpg");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: contain;
    color: #2c3e50;
    padding: 15px 0;
    background-color: #d9f0ff;
}
#app .container {
    width: 1336px;
    margin: 0 auto;
}
#app .music-play-box {
    position: fixed;
    right: -400px;
    top: 190px;
    height: calc(100% - 190px * 2);
    width: 400px;
    z-index: 5;
    transition: all 0.6s ease-in-out;
    background: #87cefa;
}
#app .music-play-box.musicBoxShow {
    right: 0;
}
#app .music-play-box a.switch {
    padding: 0.5em;
    width: auto;
    height: auto;
    position: absolute;
    right: 100%;
    top: calc(50% - 1em);
}
}
@media screen and (max-width: 1366px) {
#app {
    height: 100%;
    background-image: url("/TeamSII/dist/static/images/bg.jpg");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: contain;
    color: #2c3e50;
    padding: 15px 0;
    background-color: #d9f0ff;
}
#app .container {
    width: 100%;
    margin: 0 auto;
    padding: 0 10px;
}
}


