
.switch[data-v-29c893df] {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 20px;
  background-color: rgba(0,0,0,0.25);
  border-radius: 20px;
  transition: all 0.3s;
}
.switch[data-v-29c893df]::after {
  content: attr(off-text);
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 18px;
  background-color: #fff;
  top: 1px;
  left: 1px;
  transition: all 0.3s;
  text-indent: 22px;
  font-size: 12px;
  line-height: 18px;
  color: #fff;
}
input[type='checkbox'][data-v-29c893df] {
  display: none;
}
input[type='checkbox']:checked + .switch[data-v-29c893df]::after {
  left: 11px;
  transform: translateX(20px);
  content: attr(onText);
  text-indent: -20px;
  font-size: 12px;
  line-height: 18px;
  color: #fff;
}
input[type='checkbox']:checked + .switch[data-v-29c893df] {
  background-color: #a9cc29;
}
.offscreen[data-v-29c893df] {
  position: absolute;
}


